@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  height: 100vh;
  margin: 0;
}

.myApp {
  height: 93vh;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  text-decoration: none;
}

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 128px 0 316px;
  color: #e50914;
  background-color: black;
}

.main h1 {
  font-size: 50px;
}

.main p {
  margin-top: -32px;
}

.main button {
  margin-top: 16px;
  border: none;
  padding: 8px 96px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #e50914;
  color: #f5f5f1;
  cursor: pointer;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  z-index: 10;
}

.navbar a {
  padding: 16px;
  color: #f5f5f1;
}

.navigation {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  background-color: #221f1f;
}

.home {
  background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.8) 0%,
      rgba(255, 255, 255, 0) 50%,
      rgba(0, 0, 0, 0.8) 100%
    ),
    url("https://hcdevilsadvocate.com/wp-content/uploads/2019/01/netflix-background-9-900x506.jpg");
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.browse-container {
  position: absolute;
  bottom: 50%;
  left: 47%;
}

.browse {
  position: relative;
  height: 30px;
  width: 68px;
  background-color: #f5f5f1;
  padding: 6px 30px 6px 30px;
  cursor: pointer;
  overflow: hidden;
  z-index: 1;
  box-shadow: inset 0 0 20px rgba(255, 166, 170, 0);
  outline: 1px solid;
  outline-color: rgba(255, 166, 170, 0.5);
  outline-offset: 0px;
  text-shadow: none;
  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
}

.browse-link {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  width: inherit;
  position: absolute;
  text-decoration: none;
  color: #e50914;
  z-index: 1;
  transition: color 0.2s;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-white {
  color: #f5f5f1;
}

.background-button {
  position: absolute;
  width: 0;
  height: 0;
  background-color: #e50914;
  border-radius: 100px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  transition: width 0.3s, height 0.3s, box-shadow 0.3s;
}

.is-hover {
  width: 200px;
  height: 200px;
  box-shadow: inset 0px 0px 300px -40px rgba(0, 0, 0, 1);
}

.browse:hover {
  box-shadow: inset 0 0 20px rgba(255, 166, 170, 0.5),
    0 0 20px rgba(255, 126, 133, 0.2);
  outline-color: rgba(255, 166, 170, 0);
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
}

.footer {
  text-align: center;
  background-color: #221f1f;
  padding: 8px 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}

.movie-wrapper {
  display: flex;
  overflow-x: scroll;
}

.tile {
  padding: 16px;
  display: flex;
  flex-direction: column;
}

.tile-title {
  text-align: center;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.favorite {
  color: #f6d70b;
}

.like-button {
  border: none;
  border-radius: 12%;
  padding: 2px 4px;
  font-family: "Roboto Condensed" sans-serif;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #e50914;
  color: #f5f5f1;
  cursor: pointer;
}

.tile-like {
  margin: auto;
  margin-top: 8px;
}

.movies {
  background-color: black;
  color: #f5f5f1;
  padding: 12px;
  position: relative;
}

.movie-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10vh 0;
}

.movie-rating {
  text-align: "center";
  margin: auto;
  display: block;
}

.movie-details {
  background-color: black;
  color: #f5f5f1;
  padding: 12px;
  position: relative;
}

.about {
  display: flex;
  width: 100%;
  background-color: black;
  padding: 12px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #f5f5f1;
  text-decoration: none;
  text-align: center;
}

.about-header {
  color: #e50914;
}

.about-header-yellow {
  color: #f6d70b;
}

.about-position {
  font-size: 20px;
}

.about-summary {
  text-align: center;
  margin: 0 384px;
}

.flexit {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}

.about-detail {
  margin-top: 48px;
}

.link {
  color: #f5f5f1;
}

.change-button {
  margin-top: 16px;
  border: none;
  padding: 4px 8px;
  font-family: "Roboto Condensed" sans-serif;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #e50914;
  color: #f5f5f1;
  cursor: pointer;
}

.change-button-yellow {
  margin-top: 16px;
  border: none;
  padding: 4px 8px;
  font-family: "Roboto Condensed" sans-serif;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #f6d70b;
  color: black;
  cursor: pointer;
}

.contact {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: #f5f5f1;
  padding-bottom: 48px;
}

.child-container {
  text-align: center;
  flex-direction: column;
  display: flex;
  align-content: center;
  justify-content: center;
}

.child-input {
  border-radius: 12px;
  padding: 12px 16px;
  width: 768px;
  text-align: center;
}

.error {
  margin-bottom: 16px;
  padding: 4px 0;
}

.message {
  border-radius: 12px;
  padding: 12px 16px;
  width: 768px;
  text-align: center;
  margin-bottom: 48px;
}

.submit-button {
  border: none;
  padding: 8px 96px;
  font-family: "Roboto Condensed" sans-serif;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #e50914;
  color: #f5f5f1;
  cursor: pointer;
}

.success {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 128px 0 316px;
  color: #f6d70b;
  background-color: black;
}

.success h1 {
  font-size: 50px;
}

.success p {
  margin-top: -32px;
}

.success button {
  margin-top: 16px;
  border: none;
  padding: 8px 96px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  background-color: #e50914;
  color: #f5f5f1;
  cursor: pointer;
}

